.filterBtn_container{background-color:#eef6f6;border:none;border-radius:5px;margin:5px 5px;cursor:pointer;transition:0.3s ease}.filterBtn_container .filterBtn_text{font-family:"Spartan",sans-serif;font-weight:bolder;color:#5ba4a4;font-size:0.7rem;padding:5px 5px 5px;line-height:0.7rem}.filterBtn_container:hover{background-color:#5ba4a4}.filterBtn_container:hover .filterBtn_text{color:#eef6f6}.filterBtn_active{background-color:#5ba4a4}.filterBtn_active .filterBtn_text{color:#eef6f6}

.filter_wrapper{margin:auto;animation-name:popUp;animation-play-state:running;animation-duration:0.4s;animation-iteration-count:1;transform:0.2s ease}.filter_wrapper .filter_container{display:grid;grid-template-columns:1fr 80px}.filter_wrapper .filter_container .filter_selectedOverview{display:flex;flex-wrap:wrap;flex-shrink:1;flex-grow:1}.filter_wrapper .filter_clearBtnWrapper{display:flex;align-items:center}.filter_wrapper .filter_clearBtnWrapper .filter_clearBtn{background-color:transparent;border:none;height:50px;width:100%;font-size:0.9rem;color:#7b8e8e;transition:0.2s ease;font-weight:600;cursor:pointer}.filter_wrapper .filter_clearBtnWrapper .filter_clearBtn p{position:relative}.filter_wrapper .filter_clearBtnWrapper .filter_clearBtn p::after{content:'';display:flex;width:0px;height:1.5px;transition:0.1s ease;background-color:#7b8e8e;position:absolute;transform:translateX(-50%);left:50%;bottom:0px}.filter_wrapper .filter_clearBtnWrapper .filter_clearBtn:hover{color:#5ba4a4}.filter_wrapper .filter_clearBtnWrapper .filter_clearBtn:hover p::after{background-color:#5ba4a4;width:40px}@keyframes popUp{0%{transform:translateY(100%);opacity:0}100%{transform:translateY(0%)}}

